@import "@/assets/scss/config/_colors.scss";










































































































.m-bottom_0 {
  m-bottom: 0 !important;
}

.logo-lockup {
  display: flex;
  flex-direction: column;
  @media only screen and (min-width: 500px) {
    flex-direction: row;
  }
}

.logo-lockup .logo {
  height: 80px;
  flex: 0 1 0px;
}

.logo-lockup .text {
  flex: 1 1 auto;
  line-height: 1;
  font-size: 40px;
  align-self: center;
  br-top: 3px solid #00386b;
  padding-top: 1rem;
  text-align: center;
  color: #00386b;
  @media only screen and (min-width: 500px) {
    br-l: 3px solid #00386b;
    p-l: 1rem;
    text-align: l;
    padding-top: 0;
    br-top: 0 none;
  }
}
